/* Navbar.css */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #007bff; /* Change the background color as needed */
  padding: 10px 20px;
  color: #ffffff; /* Change the text color as needed */
  margin-bottom: 40px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 200px; /* Adjust the logo size as needed */
  height: 100px;
  margin-right: 10px;
}

.logo h2 {
  font-size: 20px; /* Adjust the font size as needed */
  font-weight: bold;
  margin: 0;
}

.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin-right: 20px;
}

.nav-links li:last-child {
  margin-right: 0;
}

.nav-links a {
  color: #ffffff; /* Change the link color as needed */
  text-decoration: none;
  font-size: 16px; /* Adjust the font size as needed */
  font-weight: bold;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #f0f0f0; /* Change the link color on hover as needed */
}
