/* CSS for PokemonDetails Component */
.poke-card {
  padding: 20px;
  border-radius: 10px;
  margin: 20px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0; /* Add a background color to the entire card */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Add a subtle box shadow */
}

.poke-card img {
  width: 200px;
  height: 200px;
  display: block;
  margin: 0 auto;
  border-radius: 50%; /* Make the image circular */
  border: 5px solid white; /* Add a border to the image */
}

.poke-card h2 {
  font-size: 28px;
  text-align: center;
  margin-top: 10px;
}

.poke-card p {
  font-size: 16px;
  text-align: center;
  margin: 5px 0;
}

/* Flexbox for Sections */
.sections-container {
  display: flex;
  flex-wrap: wrap; /* Allow sections to wrap to the next row */
  justify-content: space-between; /* Distribute sections evenly in a row */
}

.section {
  flex: 1; /* Make each section take an equal amount of space */
  margin: 20px;
  padding: 20px;
  border-radius: 10px;
  background-color: #ffffff; /* Add a background color to each section */
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2); /* Add a subtle box shadow to each section */
  max-width: 45%; /* Limit the width of each section */
}

.section h3 {
  font-size: 24px;
  margin-bottom: 10px;
}

.section ul {
  list-style: none;
  padding: 0;
}

.poke-details .section ul li {
  font-size: 16px;
  margin: 5px 0;
  text-align: left;
}

.poke-details .section {
    margin: auto;
    justify-content: space-around;
}